<template>
  <section class="plans">
    <div class="row">
      <h2>Start eating healthy today</h2>
    </div>
    <div class="row">
      <div v-for="plan in plans">
        <div class="col span-1-of-3">
          <div class="plans-box">

            <div>
              <h3> {{ plan.type }}</h3>
              <p class="plans-box-price" v-html="plan.price"></p>
              <p class="plans-box-price-meal"> {{ plan.mealPrice }}</p>
            </div>

            <div>
              <ul class="plans-box-content">
                <li v-for="item in plan.content" class="plans-box-content-item">
                  <i class="fa fa-check icon-mini"></i>
                  {{ item }}
                </li>
              </ul>
            </div>

            <div>
              <a href="#" :class="plan.btn">Sign up now</a>
            </div>

          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
  export default {
    data () {
      return {
        plans: [
          {
            type: 'Premium',
            price: `399$ <span>/ month</span>`,
            mealPrice: 'That’s only 13.30$ per meal',
            content: [
              '1 meal every day',
              'Order 24/7',
              'Access to newest creations',
              'Free delivery'
            ],
            btn: {
              'btn': true,
              'btn-full': true
            }
          },
          {
            type: 'Pro',
            price: `149$ <span>/ month</span>`,
            mealPrice: '1 meal 10 days/month',
            content: [
              '1 meal',
              'Order from 8 am to 12 pm',
              'Free delivery'
            ],
            btn: {
              'btn': true,
              'btn-ghost': true
            }
          },
          {
            type: 'Starter',
            price: `19$ <span>/ month</span>`,
            mealPrice: '1 meal/month',
            content: [
              'Order from 8 am to 12 pm',
              'Free delivery'
            ],
            btn: {
              'btn': true,
              'btn-ghost': true
            }
          }
        ]
      }
    }
  }
</script>



