<template>
  <section class="cities">
    <div class="row">
      <h2>We're currently in these cities</h2>
    </div>
    <div id="js--wp-3" class="row">
      <div class="col span-1-of-4 cities-box" v-for="city in cities">
        <img :src="require(`../../assets/images/${city.image}.jpg`)" :alt="city.city">
        <h3> {{ city.city }}</h3>
        <div class="cities-box-feature">
          <i class="fa fa-user icon-small"></i>
          {{ city.eaters }}
        </div>
        <div class="cities-box-feature">
          <i class="fa fa-star icon-small"></i>
          {{ city.chefs }}
        </div>
        <div class="cities-box-feature">
          <i class="fa fa-twitter icon-small"></i>
          <a href="#">{{ city.twitter }}</a>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
  /* eslint-disable no-unused-vars */

  import Waypoint from 'waypoints-mrd'
  import $ from 'jquery'

  export default {
    mounted () {
      let wp3 = new Waypoint({
        element: document.getElementById('js--section-cities'),
        offset: '20%',
        handler: () => {
          $('#js--wp-3').addClass('animated fadeInUpBig')
        }
      })
    },
    data () {
      return {
        cities: [
          {
            city: 'Lisbon',
            image: 'lisbon-3',
            eaters: '1600+ happy eaters',
            chefs: '60+ top chefs',
            twitter: '@omnifood_lx'
          },
          {
            city: 'San Francisco',
            image: 'san-francisco',
            eaters: '3700+ happy eaters',
            chefs: '160+ top chefs',
            twitter: '@omnifood_sf'
          },
          {
            city: 'Berlin',
            image: 'berlin',
            eaters: '2300+ happy eaters',
            chefs: '100+ top chefs',
            twitter: '@omnifood_berlin'
          },
          {
            city: 'London',
            image: 'london',
            eaters: '1200+ happy eaters',
            chefs: '50+ top chefs',
            twitter: '@omnifood_london'
          }
        ]
      }
    }
  }
</script>



