<template>
  <div id="app">
    <app-header v-show="isSticky"></app-header>
    <sticky-header v-show="!isSticky"></sticky-header>
    <features id="js--section-features"></features>
    <fav-meals id="js--section-meals"></fav-meals>
    <how-it-works id="js--section-steps"></how-it-works>
    <our-cities id="js--section-cities"></our-cities>
    <testimonials id="js--section-testimonials"></testimonials>
    <plans id="js--section-plans"></plans>
    <contact id="js--section-contact"></contact>
    <app-footer></app-footer>
  </div>
</template>

<script>
  /* eslint-disable no-unused-vars */

  import Waypoint from 'waypoints-mrd'

  import HowItWorks from './components/sections/Steps.vue'
  import FavMeals from './components/sections/Meals.vue'
  import Features from './components/sections/Features.vue'
  import StickyHeader from './components/sections/StickyNav.vue'
  import Header from './components/sections/Header.vue'
  import OurCities from './components/sections/Cities.vue'
  import Testimonials from './components/sections/Testimonials.vue'
  import Plans from './components/sections/Plans.vue'
  import Contact from './components/sections/Contact.vue'
  import Footer from './components/sections/Footer.vue'
  export default {
    mounted () {
      let self = this
      let mainWp = new Waypoint({
        element: document.getElementById('js--section-features'),
        offset: '60px',
        handler: function (direction) {
          if (direction === 'down') {
            self.isSticky = false
          } else {
            self.isSticky = true
          }
        }
      })
    },
    name: 'app',
    data () {
      return {
        isSticky: true
      }
    },
    components: {
      appHeader: Header,
      StickyHeader,
      Features,
      FavMeals,
      HowItWorks,
      OurCities,
      Testimonials,
      Plans,
      Contact,
      appFooter: Footer
    }
  }
</script>






