<template>
  <section class="testimonials">
    <div class="row">
      <h2>Our customers can't live without us</h2>
    </div>
    <div class="row">
      <div v-for="customer in customers" class="col span-1-of-3">
        <blockquote class="testimonials-quote">
          {{ customer.quote }}
          <cite class="testimonials-quote-cite">
            <img :src="require(`../../assets/images/${customer.image}.jpg`)" alt="">
            {{ customer.name }}
          </cite>
        </blockquote>
      </div>
    </div>
  </section>
</template>
<script>
  export default {
    data () {
      return {
        customers: [
          {
            quote: 'Omnifood is just awesome! I just launched a startup which leaves me with no time for cooking, so Omnifood is a life-saver. Now that I got used to it, I couldn\'t live without my daily meals!\n',
            image: 'customer-1',
            name: 'Alberto Duncan'
          },
          {
            quote: 'Inexpensive, healthy and great-tasting meals, delivered right to my home. We have lots of food delivery here in Lisbon, but no one comes even close to Omifood. Me and my family are so in love!\n',
            image: 'customer-2',
            name: 'Joana Silva'
          },
          {
            quote: 'I was looking for a quick and easy food delivery service in San Franciso. I tried a lot of them and ended up with Omnifood. Best food delivery service in the Bay Area. Keep up the great work!\n',
            image: 'customer-3',
            name: 'Milton Chapman'
          }
        ]
      }
    }
  }
</script>

