<template>
  <section class="features">
    <div class="row">
      <h2> {{ headings[0] }}</h2>
      <p class="long-copy"> {{ longCopy[0] }}</p>
    </div>
    <div id="js--wp-1" class="row">
      <div class="col span-1-of-4 box">
        <i class="fa fa-star-o icon-big"></i>
        <h3> {{ headings[1] }}</h3>
        <p> {{ longCopy[1] }}</p>
      </div>

      <div class="col span-1-of-4 box">
        <i class="fa fa-clock-o icon-big"></i>
        <h3> {{ headings[2] }}</h3>
        <p> {{ longCopy[2] }}</p>
      </div>

      <div class="col span-1-of-4 box">
        <i class="fa fa-leaf icon-big"></i>
        <h3> {{ headings[3] }}</h3>
        <p> {{ longCopy[3] }}</p>
      </div>

      <div class="col span-1-of-4 box">
        <i class="fa fa-shopping-cart icon-big"></i>
        <h3> {{ headings[4] }}</h3>
        <p> {{ longCopy[4] }}</p>
      </div>
    </div>
  </section>
</template>
<script>
  /* eslint-disable no-unused-vars */

  import Waypoint from 'waypoints-mrd'
  import $ from 'jquery'
  export default {
    mounted () {
      let wp1 = new Waypoint({
        element: document.getElementById('js--section-features'),
        offset: '60px',
        handler: () => {
          $('#js--wp-1').addClass('animated fadeInLeft')
        }
      })
    },
    data () {
      return {
        headings: [
          'Get food fast \u2014 not fast food.',
          'Up to 365 days/year',
          'Ready in 20 minutes',
          '100% organic',
          'Order anything'
        ],
        longCopy: [
          'Hello, we’re Omnifood, your new premium food delivery service. We know you’re always busy. No time for cooking. So let us take care of that, we’re really good at it, we promise!',
          'Never cook again! We really mean that. Our subscription plans include up to 365 days/year coverage. You can also choose to order more flexibly if that\'s your style.',
          'You\'re only twenty minutes away from your delicious and super healthy meals delivered right to your home. We work with the best chefs in each town to ensure that you\'re 100% happy.',
          'All our vegetables are fresh, organic and local. Animals are raised without added hormones or antibiotics. Good for your health, the environment, and it also tastes better!',
          'We don\'t limit your creativity, which means you can order whatever you feel like. You can also choose from our menu containing over 100 delicious meals. It\'s up to you!'
        ]
      }
    }
  }
</script>



