<template>
  <header>
    <nav class="main-nav">
      <div class="row">
        <img src="../../assets/images/logo-white.png" alt="main-logo" class="logo">
        <img src="../../assets/images/logo.png" alt="main-logo" class="logo-black">
        <transition name="slide">
          <ul v-show="menuIsActive" class="main-nav-options">
            <li v-for="section in sections">
              <span @click="goTo(section.id)">{{ section.title }}</span>
            </li>
          </ul>
        </transition>
        <span
          @click="toggleMenu"
          class="main-nav-button">
          <i class="icon-mid"
             :class="{'fa fa-bars': !menuIsActive, 'fa fa-times': menuIsActive}">
          </i>
        </span>
      </div>
    </nav>
    <div class="hero-text-box">
      <h1>Goodbye junk food.<br/>Hello super healthy meals.</h1>
      <span @click="goTo('js--section-plans')" class="btn btn-full">I’m hungry</span>
      <span @click="goTo('js--section-features')" class="btn btn-ghost">Show me more</span>
    </div>
  </header>
</template>
<script>
  export default {
    data () {
      return {
        menuIsActive: true,
        sections: [
          {
            title: 'Food Delivery',
            id: 'js--section-plans'
          },
          {
            title: 'How it works',
            id: 'js--section-steps'
          },
          {
            title: 'Our cities',
            id: 'js--section-cities'
          },
          {
            title: 'Sign up',
            id: 'js--section-contact'
          }
        ]
      }
    },
    methods: {
      toggleMenu () {
        let self = this
        self.menuIsActive = !self.menuIsActive
      },
      goTo (sectionId) {
        let self = this
        self.$SmoothScroll(document.getElementById(sectionId), 750)
      }
    }
  }
</script>
<style scoped>
  .slide-enter-active, .slide-leave-active {
    transition: opacity .5s;
  }
  .slide-enter, .slide-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
</style>



