<template>
  <footer class="footer">

    <div class="row">

      <div class="col span-1-of-2">
        <ul class="footer-nav">
          <li v-for="item in navItems">
            <a href="#"> {{ item }}</a>
          </li>
        </ul>
      </div>

      <div class="col span-1-of-2">
        <ul class="footer-social">
          <li v-for="item in socialItems">
            <a href="#">
              <i :class="item.class"></i>
            </a>
          </li>
        </ul>
      </div>
    </div>

    <div class="row">
      <p class="footer-legal">
        Copyright &copy; 2017 by Me.
      </p>
    </div>

  </footer>
</template>
<script>
  export default {
    data () {
      return {
        navItems: [
          'About us',
          'Blog',
          'Press',
          'iOS App',
          'Android App'
        ],
        socialItems: [
          {class: 'fa fa-facebook-square fb-icon icon-mid'},
          {class: 'fa fa-twitter twitter-icon icon-mid'},
          {class: 'fa fa-google-plus googlePlus-icon icon-mid'}
        ]
      }
    }
  }
</script>



