<template>
  <section class="steps">
    <div class="row">
      <h2>How it works &dash; Simple as 1, 2, 3</h2>
    </div>
    <div class="row">
      <div class="col span-1-of-2 step-box">
        <img id="js--wp-2"
             class="steps-appStore"
             src="../../assets/images/app-iPhone.png"
             alt="Omnifood on iPhone">
      </div>
      <div class="col span-1-of-2 step-box">
        <div v-for="step in steps" class="steps-step">
          <div>{{ step.number }}</div>
          <p>{{ step.description }}</p>
        </div>
        <a href="" class="steps-step-btn">
          <img src="../../assets/images/download-app.svg" alt="Appstore Button">
          <img src="../../assets/images/download-app-android.png" alt="Google Play Button">
        </a>
      </div>
    </div>
  </section>
</template>
<script>
  /* eslint-disable no-unused-vars */

  import Waypoint from 'waypoints-mrd'
  import $ from 'jquery'

  export default {
    mounted () {
      let wp2 = new Waypoint({
        element: document.getElementById('js--wp-2'),
        offset: '50%',
        handler: () => {
          $('#js--wp-2').addClass('animated fadeInUp')
        }
      })
    },
    data () {
      return {
        steps: [
          {
            number: 1,
            description: 'Choose the subscription plan that best fits your needs and sign up today.'
          },
          {
            number: 2,
            description: 'Order your delicious meal using our mobile app or website. Or you can even call us!'
          },
          {
            number: 3,
            description: 'Enjoy your meal after less than 20 minutes. See you the next time!'
          }
        ]
      }
    }
  }
</script>



